import UserPlaylist from '@/components/UserPlaylist';
import { getUser } from "@/api/comment-api";
import { OK } from 'js/config';
import CollectSheet from 'public/CollectSheet';
export default {
  data: function data() {
    return {
      user: {
        profile: {
          backgroundUrl: '',
          avatarUrl: '',
          nickname: '',
          followeds: 0
        },
        listenSongs: 0
      },
      text: '去TA的个人主页',
      isLoading: true
    };
  },
  components: {
    UserPlaylist: UserPlaylist,
    CollectSheet: CollectSheet
  },
  computed: {
    uid: function uid() {
      return this.$route.query.uid;
    }
  },
  methods: {
    _getUser: function _getUser(id) {
      var _this = this;

      getUser(id).then(function (res) {
        if (res.status === OK) {
          _this.user = res.data;
          _this.isLoading = false; // console.log(this.isLoading)

          _this.$store.dispatch('user/setUserId', res.data.profile.userId);
        }
      });
    }
  },
  created: function created() {
    this._getUser(this.uid);
  }
};